.team__container {
  h1 {
    text-align: center;
  }
  .members {
    width: 80vw;

    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    align-items: center;

    .member__card {
      display: flex;
      flex-flow: column;
      align-items: center;

