@import url(https://fonts.googleapis.com/css?family=Roboto:300);
*{box-sizing:border-box}html,body{margin:0;padding:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:16px}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
*{margin:0;padding:0;box-sizing:border-box}html,body{font-size:1rem;margin:0;padding:0;background-color:#fff;font-size:16px}.team__container h1{text-align:center}.team__container .members{width:80vw;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;margin:0 auto;-webkit-align-items:center;align-items:center}.team__container .members .member__card{display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column;-webkit-align-items:center;align-items:center}.team__container .members .member__card .member__picture{width:20rem;height:20rem;border-radius:50%;background-color:#fff;overflow:hidden;box-shadow:0 6px 20px rgba(15,56,133,.17)}.team__container .members .member__card .member__picture img{width:100%;height:100%;object-fit:contain}.team__container .members .member__card .member__info{padding:1rem 0}.card{position:relative;min-width:300px;height:auto;overflow:hidden;border-radius:15px;margin:0 auto;padding:40px 20px;box-shadow:0 10px 15px rgba(0,0,0,.3);transition:.5s}.card:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.card_red,.card_red .title .fa{background:linear-gradient(-45deg, #ffec61, #f321d7)}.card_violet,.card_violet .title .fa{background:linear-gradient(-45deg, #f403d1, #64b5f6)}.card_three,.card_three .title .fa{background:linear-gradient(-45deg, #24ff72, #9a4eff)}.card:before{content:"";position:absolute;bottom:0;left:0;width:100%;height:40%;background:rgba(255,255,255,.1);z-index:1;-webkit-transform:skewY(-5deg) scale(1.5);transform:skewY(-5deg) scale(1.5)}.title .fa{color:#fff;font-size:60px;width:100px;height:100px;border-radius:50%;text-align:center;line-height:100px;box-shadow:0 10px 10px rgba(0,0,0,.1)}.title h2{position:relative;margin:20px 0 0;padding:0;color:#fff;font-size:28px;z-index:2}.price{position:relative;z-index:2}.price h4{margin:0;padding:20px 0;color:#fff;font-size:60px}.option{position:relative;z-index:2}.option ul{margin:0;padding:0}.option ul li{margin:0 0 10px;padding:0;list-style:none;color:#fff;font-size:16px}.card a{display:block;position:relative;z-index:2;background-color:#fff;color:#262fff;width:150px;height:40px;text-align:center;margin:20px auto 0;line-height:40px;border-radius:40px;font-size:16px;cursor:pointer;text-decoration:none;box-shadow:0 5px 10px rgba(0,0,0,.1)}
.navbar{box-sizing:border-box;background-color:#fff}.navbar .container-fluid{width:80%;margin:auto}.navbar .container-fluid a.navbar-brand{color:#000;font-size:1rem;margin:0 0 0 2rem}.navbar .container-fluid .navbar-collapse .navbar-nav{text-align:justify;text-align-last:center}.navbar .container-fluid .navbar-collapse .navbar-nav .nav-item{border-radius:3rem;padding:1rem}.navbar .container-fluid .navbar-collapse .navbar-nav .nav-item a.nav-link{color:#000;font-size:1rem}.navbar .container-fluid .navbar-collapse .navbar-nav .nav-item:hover{background-color:#b483fb;box-shadow:0 6px 20px rgba(15,56,133,.17)}.navbar .container-fluid .navbar-collapse .navbar-nav .nav-item:hover a.nav-link{color:#fff}@media screen and (max-width: 768px){.navbar .container-fluid{width:100%;margin:auto}}
